  box-shadow: rgb(31 31 31 / 12%) 0px 1px 6px, rgb(31 31 31 / 12%) 0px 1px 4px;
  border-radius: 10px;
  padding: 12px;
  margin: 20px;
  width: 180px;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.cardInAccnt > h3 {
  font-size: 19px;
  font-weight: 400;
}
.cardInAccnt > button {
  padding: 3px 6px;
  margin-top: 10px;
  border-radius: 50px;
  color: white;
  background-color: rgb(49, 52, 21) !important;
  border: 2px solid rgb(49, 52, 21) !important;
  width: 86px;
  outline-width: 0;
  cursor: pointer;
}

.myArticles {
  width: 80vw;
  height: 100vh;
  overflow: scroll;
}
.myArticles > h1 {
  font-size: 30px !important;
  font-weight: 400 !important;
}

.myJobs {
  width: 80vw;
  height: 100vh;
  overflow: scroll;
}
.myJobs > h1 {
  font-size: 30px !important;
  font-weight: 400 !important;
}

