.signup {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.signup-left {
  font-size: 16px;
  flex: 0.5;
  height: 100%;
}
.signup-left > h3 {
  margin: 15px 25px;
  color: #383e56;
  font-weight: 300;
}
.signup-right {
  background-color: #ecf5e9;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0.5 1;
  margin: 20px 40px;
  clip-path: polygon(21% 0, 100% 0, 100% 100%, 0% 100%);
}
.signup-right > h1 {
  color: #383e56;
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 30px;
}
.signup-right > input {
  background-color: #ffffff;
  border-radius: 50px;
  font-size: 13px;
  padding: 8px 15px;
  outline-width: 0;
  border: 2px solid #383e56;
  width: 272px;
  color: white;
  margin-bottom: 30px;
}
.signup-right > select {
  background-color: #ffffff;
  border-radius: 50px;
  font-size: 13px;
  padding: 8px 15px;
  outline-width: 0;
  border: 2px solid #383e56;
  width: 306px;
  color: #383e56;
  margin-bottom: 30px;
}
.signup-right > button {
  background-color: #383e56;
  border-radius: 50px;
  font-size: 16px;
  padding: 7px 15px;
  outline-width: 0;
  border: 2px solid #feffde;
  width: 272px;
  margin-bottom: 30px;
  color: #ffffff;
}
.signup-leftImages {
  height: -webkit-fill-available;
  width: -webkit-fill-available;
  background-image: url("./assets/41464-student-with-books.gif");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}
.account {
  color: #383e56;
  font-size: 15px;
  margin-bottom: 25px;
  cursor: pointer;
}
.signIn {
  color: #383e56;
  font-size: 15px;
  cursor: pointer;
  margin-bottom: 25px;
  text-decoration: none;
}
.forget {
  color: #383e56;
  font-size: 12px;
  margin-bottom: 15px;
  text-decoration: underline;
  cursor: pointer;
  text-decoration: none;
}
